.body-big-title {
  font-size: 120px;
}

.body-title {
  font-size: 40px;
}

.description-text {
  margin-top: 30px;
  line-height: 2em;
  width: 1200px;
  font-size: 24px;
}

.App-header-text {
  margin-right: 150px;
}

.header-description-text {
  margin-top: 50px;
  font-size: 35px;
}

.App-header-photo {
  width: 600px;
  height: 800px;
}

.menu-item {
  font-size: 28px;
  border-radius: 8px;
  width: 500px;
  margin-top: 25px;
  padding: 15px 30px;
}

.App {
  text-align: center;
  background-color: #ffffff;
  position: relative;
}

.App-header {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: row;
  text-align: left;
  justify-content: center;
}

.App-header-text {
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
}

.App-header-photo {
  background-color: #f5f5f5;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.header-item-col {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  align-self: center;
  top: 0px;
  right: 0px;
  width: 100vw;
  padding-right: 25px;
  z-index: 4;
  background: rgb(255, 255, 255);
  border-bottom: 1px solid rgb(231, 231, 231);
  color: rgb(0, 0, 0);
}

.header-item {
  padding: 15px 25px;
  text-align: right;
  font-family: "Work Sans";
  font-weight: 400;
  cursor: pointer;
}

.header-item:hover {
  font-weight: 600;
}

.App-body {
  display: flex;
  flex-direction: column;
}

.header-content {
  display: flex;
  flex-direction: row;
}

.photo {
  border-radius: 50%;
  background: white;
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.body-menu {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 175px;
  margin-right: 325px;
  height: 680px;
  justify-content: space-between;
  color: black;
}

.body-about {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  height: 100vh;
  align-self: flex-start;
  color: white;
}

.body-about-img-wrapper {
  position: absolute;
}

.body-about-img {
  height: 850px;
  width: 99.4vw;
  object-fit: cover;
}

.body-big-title {
  text-align: left;
  font-family: "Work Sans";
  line-height: 0.8em;
}

.body-about-description {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 175px;
  z-index: 2;
}

.description-text {
  text-align: left;
  font-family: "Roboto Slab";
  font-weight: 300;
}

.header-description-text {
  font-family: "Roboto Slab";
  font-weight: 200;
  margin-left: 10px;
}

.body-contents {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  color: white;
}

.body-contents-img-wrapper {
  position: absolute;
}

.body-contents-img {
  height: 850px;
  width: 99.4vw;
  object-fit: cover;
}

.body-menu .menu-item:last-child {
  margin-right: 0;
}

.body-header {
  text-align: left;
  z-index: 2;
  margin-left: 175px;
}

.body-title {
  font-family: "Work Sans";
  font-weight: 800;
  color: inherit;
  text-align: left;
}

.menu-item {
  box-shadow: 1px 1px 10px rgb(0, 0, 0);
  text-align: left;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
}

.menu-item-text {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.menu-item-title {
  font-family: "Work Sans";
  font-weight: 900;
  margin-right: 15px;
  padding: 0px 10px 0px 0px;
  font-size: 20px;
  white-space: nowrap;
}

.menu-item-role {
  font-family: "Work Sans";
  font-weight: 400;
  font-size: 12px;
  width: 300px;
  text-align: right;
}

.menu-item-status {
  font-family: "Work Sans";
  font-weight: bold;
  font-size: 12px;
  padding: 3px 7px;
  text-align: left;
  border-radius: 12px;
  color: white;
}

.menu-item-description {
  font-family: "Work Sans";
  font-size: 14px;
  font-weight: 400;
  margin-top: 10px;
}

.menu-item-breakdown {
  font-family: "Work Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 2;
  margin-top: 10px;
}

.menu-item-demo {
  font-weight: 600;
  font-family: "Work Sans";
  font-size: 14px;
  margin-top: 20px;
  text-transform: uppercase;
  cursor: pointer;
}

.menu-item-demo:hover {
  font-weight: 900;
}

.link {
  text-decoration: none;
  color: black;
}

.body-qualifications {
  display: flex;
  height: 100vh;
  flex-direction: row;
  align-items: center;
  position: relative;
  color: white;
}

.body-qualifications-img-wrapper {
  position: absolute;
}

.body-qualifications-img {
  height: 850px;
  width: 99.4vw;
  object-fit: cover;
}

.body-qualifications-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  z-index: 2;
  margin-left: 175px;
  margin-right: 175px;
  height: 775px;
  width: 750px;
  padding: 20px 0px;
  border-radius: 12px;
}

.body-qualifications-main-title {
  font-family: "Work Sans";
  line-height: 2em;
  font-size: 19px;
  text-transform: uppercase;
  font-weight: 900;
}

.body-qualifications-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px 25px 15px 25px;
  color: black;
  background: white;
  margin-right: 15px;
  box-shadow: 1px 1px 10px black;
  border-radius: 12px;
}

.body-qualifications-lang-img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
  object-fit: contain;
  align-self: center;
}

.body-qualifications-langs-grid {
  display: flex;
  flex-direction: column;
}

.body-qualifications-proficiency-langs {
  display: flex;
  flex-direction: row;
  margin-top: 5px;
}

.body-qualifications-proficiency-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 25px;
  margin-right: 15px;
  border-radius: 12px;
  padding: 10px 15px 10px 0px;
  background: rgb(255, 255, 255);
  color: black;
}

.body-qualifications-lang-title {
  font-family: "Work Sans";
  font-size: 15px;
  line-height: 2em;
  font-weight: 500;
  text-transform: uppercase;
}

.kishek {
  position: fixed;
  top: 10px;
  left: 25px;
  font-family: "Work Sans";
  font-size: 20px;
  font-weight: 900;
  z-index: 5;
  color: black;
}

.socials {
  position: fixed;
  top: 50%;
  right: 0px;
  transform: translate(0%, -50%);
  z-index: 4;
}

.socials-row {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: white;
  padding: 10px 5px;
  border-radius: 12px 0px 0px 12px;
}

.socials-img-wrapper {
  width: 40px;
  height: 40px;
}

.socials-img {
  width: 25px;
  height: 25px;
  object-fit: cover;
  border-radius: 100%;
  margin-top: 3px;
  padding: 2px;
  box-shadow: 1px 1px 8px rgb(229, 229, 229);
}

.socials-img:hover {
  width: 30px;
  margin-top: 0px;
  height: 30px;
}
